.content-section {
  display: flex;
  justify-content: space-between;
  background-color: black;
  height: 100vh;
  width: 100vw;
  margin: 0;
  overflow: hidden;
}

.section-text-container {
  width: 80vw;
  line-height: 3em;
  margin-top: -0.5em;
  margin-left: 0em;
}

.section-text {
  font-size: 3em;
  margin-left: 1em;
  color: white;
}

@media (max-width: 600px) {
  .section-text-container {
    line-height: 2em;
  }

  .section-text {
    font-size: 16px;
  }

  .content-section {
    height: 150vh;
  }

  .content-section {
    height: 90vh;
  }
}

@media (width: 320px) {
  .content-section {
    height: 120vh;
  }
}
